import {fetch as fetchPolyfill} from 'whatwg-fetch';
window.fetch = fetchPolyfill;

import { Component} from '@angular/core';
import { ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { Platform }             from '@ionic/angular';
import { Events }               from './providers/events-service';
import { ToastController }      from '@ionic/angular';
import { PopoverController }    from '@ionic/angular';

import { StatusBar } from '@ionic-native/status-bar/ngx';
import { Network } from '@ionic-native/network/ngx';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';


import { SettingsPage } from './pages/settings/settings.page';

import { PlantService }         from './providers/plant-service';
import { OperatorService }      from './providers/operator-service';
import { ConfigurationService } from './providers/configuration-service';
import { TransactionService }   from './providers/transaction-service';
import { TankService }         from './providers/tank-service';
import { TimesheetService }     from './providers/timesheet-service';
import { DipReadingService }     from './providers/dipreading-service';
import { BluetoothService }     from './providers/bluetooth-service';
import { Storage } from '@ionic/storage-angular';



declare var bluetoothle:any;

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent {

    public rootPage: any;

    constructor(private platform: Platform,
        protected toastController: ToastController,
        protected popoverController: PopoverController,
        protected events: Events,
        private splashScreen: SplashScreen,
        private network:Network,
        private statusBar: StatusBar,
        private router: Router, private storage:Storage) {
		


		alert("here");

        this.platform.ready().then(() => {
            


			
            this.splashScreen.hide();


      
            if (this.platform.is("android")) {
                //this.statusBar.backgroundColorByHexString("#952517");
                //this.statusBar.styleLightContent();
                this.statusBar.styleDefault();
            }
            else{
                this.statusBar.styleLightContent();
            }
            

			
        });

    }
    

    
    
    protected onNetworkConnected() {
        console.log('[FuelITApp] - onNetworkConnected() :: Network connection detected.');
        this.events.publish('database:sync');
    }

    protected onNetworkDisconnected() {
        console.log('[FuelITApp] - onNetworkDisconnected() :: Network disconnected.');
    }

    async settings(event) {
        
        const popover = await this.popoverController.create({
          component: SettingsPage,
          componentProps:            
              {
            },
          cssClass: 'popover-settings',
          event: event,
          translucent: true
        });
        await popover.present();

        const { data, role } = await popover.onDidDismiss();

        
        if (data && data.refresh) {
            this.router.navigateByUrl('/');
        }        
        
        
    }    
        

    

    protected logout() {

    }

    protected sync() {
        console.log('[FuelITApp] - sync() :: Synchronizing databases.');

    }
}
